export const initialState={
    basket:[],
    user:null
}
const sum=(amount,item)=>item.price+amount      //where amount=initial value which is equal to zero
export const getBasketTotal=(basket)=>{
    return basket?.reduce(sum,0)         //there item will be basket[0],basket[1].... due to mapping by reduce
}

const reducer=(state,action)=>{
    console.log(action.user)
    switch(action.type){
        case'ADD_TO_BASKET':
        return {
            ...state,
            basket:[...state.basket,action.item],
        }
        case"REMOVE_FROM_BASKET":
        const index=state.basket.findIndex((basketItem)=>
        basketItem.id===action.id)                  //it will find the index of the item to be removed

        let newBasket=[...state.basket]
        if(index>=0){
         newBasket.splice(index,1)
        }
        else{
            console.warn(`Cannot remove product (id:${action.id}) as its not in basket`)
        }
        return {
            ...state,
            basket:newBasket
        }
        case"EMPTY_BASKET":
        return {
            ...state,
            basket:[]
        }
        case"SET_USER":
        return{
            ...state,
            user:action.user
        }
        default:
            return state
    }
    
    
}
export default reducer